// Top head start ///////////////////////////////////////////////////

.top-head-1 {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 13px;
	flex-wrap: wrap;
	padding-bottom: 0;
	background-color: $color-light-back-2 !important;
}

.top-head-1 .top-left-content,
.top-head-1 .trackorderandauthlinks {
	display: flex;
	align-items: center;
	margin-bottom: 13px;
	line-height: 1.6;
}

.top-head-1 .top-left-content p,
.top-head-1 .trackorderandauthlinks p {
	padding: 0 10px;
	font-size: 13px;
	font-weight: 500;
	color: $color-dark-1;
}

.top-left-content span {
	font-size: 14px;
	color: $color-dark-1;
}

.top-head-1 .trackorderandauthlinks p span:hover {
	color: $color-primary-1;
	cursor: pointer;
}

.top-head-1 .trackorderandauthlinks .trackorderbutton:hover {
	color: $color-primary-1;
	cursor: pointer;
}

.top-head-1 .trackorderandauthlinks .fa {
	margin: 0 5px;
	color: $color-dark-1;
}

@media screen and (max-width: 346px) {
	.top-head-1 {
		justify-content: center;
	}
}

// Top head end ///////////////////////////////////////////////////

// Top togs start ///////////////////////////////////////////////////

.top-tags-desc {
	color: $color-dark-1;
	font-weight: 700;
	margin-left: 10px;
}

.tags {
	text-transform: capitalize;
}

@media screen and (max-width: 420px) {
	.top-tags-desc {
		margin-bottom: 10px;
	}
}

@media screen and (max-width: 600px) {
	.top-head-1 {
		justify-content: center;
	}
}

.top-tags {
	background-color: #fff !important;
	display: flex;
	padding: 13px;
	align-items: center;
	flex-wrap: wrap;
	font-size: 13px;
}

.top-tags .tags {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}

.top-tags .tags h5 {
	padding: 0 15px;
	font-weight: 500;
	line-height: 1.3;
	color: $color-dark-1;
	opacity: 1;
}

.top-tags .tags h5:hover {
	cursor: pointer;
	opacity: 0.8;
}

// Top togs end ///////////////////////////////////////////////////

/* --------------- Navbar ---------------- */
.navbar {
	position: sticky;
	top: 0;
	width: 100%;
	display: flex;
	align-items: space-between;
	z-index: 50;
	background: #fff;
}

.navbar-center {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 10px;
	flex-wrap: wrap;
}

.nav-icon {
	font-size: 22px !important;
}

// cart icon  start ///////////
.nav-icon .fa-cart {
	color: $color-dark-1;
}

.stickyCartButton {
	position: absolute;
	left: 100;
	top: 200;
	z-index: 42342342423;
	background-color: black;
}

.cartt-btn {
	position: relative;
	cursor: pointer;
}
.cartt-items {
	position: absolute;
	top: -9px;
	right: -9px;
	background: $color-primary-1;
	padding: 0 1vh;
	border-radius: 30%;
	color: #fff;
}

// cart icon  end ///////////

/* --------------- End of Navbar ---------------- */

/* --------------- Cart ---------------- */
.cart-overlay {
	position: fixed;
	top: 0;
	right: 0;
	width: 100%;
	height: 100%;
	transition: var(--mainTransition);
	background: $color-primary-overlay;
	z-index: 1001;
	// visibility: hidden;
}
.cart {
	position: fixed;
	top: 0;
	right: 0;
	width: 100%;
	height: 100%;
	overflow: scroll;
	z-index: 10010;
	background: #fff;
	// padding: 0.8rem;
	padding: 0;
	transition: var(--mainTransition);
	transform: translateX(100%);
}

.show-cart-bar {
	visibility: visible;
}

.showCart {
	transform: translateX(0);
}

@media screen and (min-width: 768px) {
	.cart {
		width: 25vw;
		min-width: 400px;
	}
}

.close-cart .fa {
	font-size: 16px;
	cursor: pointer;
	color: #aaa !important;
}

.cart h2 {
	text-transform: capitalize;
	text-align: center;
	letter-spacing: var(--mainSpacing);
	margin-bottom: 2rem;
}
/*---------- Cart Item -------------------- */

.cart-item {
	display: flex;
	align-items: center;
	padding: 5px 20px;
}

.cart-item-quantityCounter {
	margin-right: 30px;

	@media screen and (max-width: 330px) {
		margin-right: 20px;
	}
}

.cart-item img {
	width: 55px;
	height: 55px;

	@media screen and (max-width: 430px) {
		width: 35px;
		height: 35px;
	}
}

.cart-item-info {
	margin: 0 15px 0 30px;
	flex: 1;

	@media screen and (max-width: 330px) {
		margin: 0 15px 0 15px;
	}
}

.cart-item-info h4 {
	max-width: 170px;
	color: $color-dark-1;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	font-weight: 800;
	font-size: 15px;
	cursor: pointer;
	margin-bottom: 15px;
	margin-top: 15px;

	@media screen and (max-width: 430px) {
		font-size: 14px;
	}

	@media screen and (max-width: 430px) {
		max-width: 120px;
	}
}

.cart-item-info h5 {
	color: $color-primary-1;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	font-weight: 600;
	font-size: 13px;
	cursor: pointer;

	@media screen and (max-width: 430px) {
		font-weight: 500;
	}
}

.cart-item-quantityCounter {
	background-color: #fafafa;
	padding: 6px;
	border-radius: 35px;
}
.cart-item-quantityCounter .fa-chevron-up {
	margin-bottom: 10px;
	color: #aaa;
	font-size: 14px;
}
.cart-item-quantityCounter .fa-chevron-down {
	margin-top: 10px;
	color: #aaa;
	font-size: 14px;
}

.cart-item h5 {
	margin: 0.5rem 0;
	letter-spacing: var(--mainSpacing);
}
.item-amount {
	text-align: center;
	font-size: 13px;
	font-weight: 700;
}
.remove-item {
	color: #aaa;
	cursor: pointer;
	font-size: 15px;
	margin-top: 10px;
}
.fa-chevron-up,
.fa-chevron-down {
	color: $color-primary-1;
	cursor: pointer;
}

.cartOverlayHeader {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 15px 10px 18px 23px;
	border-bottom: 1px solid $color-light-1;
}

.cartOverlayHeader-left {
	display: flex;
	align-items: center;
}

.cartOverlayHeader span .fa {
	color: $color-primary-1;
}

.cartOverlayHeader span {
	color: $color-primary-1;
	font-weight: 700;
	margin-right: 10px;
}
/*---------- End of Cart Item -------------------- */

.cart-footer {
	margin-top: 2rem;
	letter-spacing: var(--mainSpacing);
	text-align: center;
}
.cart-footer h3 {
	text-transform: capitalize;
	// margin-bottom: 1rem;
}

.cart-total {
	padding: 15px 10px;

	display: flex;
	align-items: center;
	justify-content: center;
	// border-top: 2px solid #eee;
	// border-bottom: 2px solid #eee;
	margin-bottom: 20px;
}

.cart-total h3 {
	font-weight: 600;
	font-size: 15px;
}

.cart-total span {
	text-transform: uppercase;
	font-weight: 800;
	color: $color-primary-1;
	margin-left: 10px;
}

.cart-footer-checkoutButton {
	background-color: $color-primary-1;
	margin: 10px 25px;
	border-radius: 35px;
	padding: 0 0px 0 20px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	cursor: pointer;
	transition: all .5s;
}

.cart-footer-checkoutButton:hover {
	background-color: $color-primary-2;
}

.cart-footer-checkoutButton-text {
	color: #fff;
	font-size: 15px;
	font-weight: 700;
}

.cart-footer-checkoutButton-total {
	color: $color-primary-1;
	font-size: 16px;
	font-weight: 800;
	display: inline-block;
	border-radius: 40px;
	background-color: #fff;
	padding: 13px 25px;
	margin: 2px;
}
/* --------------- End of Cart ---------------- */

