/* All departments Sidemenu  //////////////////////////////////////////   */

.all-department {
	position: relative;
}

.all-department:hover .all-department-sideMenu {
	display: block;
}

.nav-menu {
	cursor: pointer;
	display: block;
	font-size: 18px;
	padding: 5px 25px 10px 25px;
	border-radius: 3px;
	background: #fff;
	border: 1px solid #fafafa;
}

.nav-menu-title {
	font-size: 14px;
	color: $color-primary-1;
	font-weight: 700;
}

.nav-menu .fa-sort-down {
	margin-left: 15px;
	font-size: 20px;
	color: $color-primary-1;
}

.all-department-sideMenu {
	position: absolute;
	height: 79%;
	width: 100%;
	font-size: 14px;
	float: left;
	z-index: 10;
	// background-color: #fff;
	display: none;
}

.all-department-sideMenu ul {
	// border: 1px solid #eff5f5;
	width: 100%;
	background-color: #fff;
	position: relative;
	min-height: 300px;
	min-width: 200px;
	top: 20px;
	right: 65px;
	border-radius: 6px;
	background-color: rgb(255, 255, 255);
	box-shadow: rgba(142, 142, 142, 0.14) 0px 3px 20px;
	z-index: 99;
	padding: 25px;

	&::before {
		display: block;
		right: 20px;
		content: " ";
		height: 0;
		top: -11px;
		width: 0;
		position: absolute;
		border-left: 11px solid transparent;
		border-right: 11px solid transparent;
		border-bottom: 11px solid #fff;
		// background-color: rgb(255, 255, 255);
	}
}

.all-department-sideMenu ul li {
	list-style: none;
	list-style-type: none;
	font-weight: bold;
	cursor: pointer;
	margin-bottom: 25px;
	color: $color-dark-1;
	font-weight: 800;
	text-transform: capitalize;
	font-size: 15px;
}

.all-department-sideMenu ul li:hover {
	color: $color-primary-1;
}

.all-department-sideMenu ul li ul {
	display: none;
	z-index: 10;
	top: 50px;
	width: 300px;
	background-color: $color-primary-1;
	position: absolute;
	top: 0;
}

.all-department-sideMenu ul li:hover ul {
	display: block;
	margin-left: 98%;
	padding: 0 100px 10px 10px;
	position: fixed;
	background: #fff;
	border-left: 1px solid #eee;
	position: absolute;
	top: 0;
	z-index: 10;
}

.fa.fa-angle-right {
	margin-top: 4px;
	margin-right: 8px;
	float: right;
	color: $color-dark-1;
}

@media screen and (max-width: 980px) {
	.all-department-sideMenu {
		margin-top: -20px;
	}

	.all-department-sideMenu ul {
		min-height: 300px;
	}
}

